<template>
  <div class="footer">
    <marquee scrollamount="10"> {{headline}}</marquee>
  </div>
</template>

<script>
import axios from "axios";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Footer',
  components: {
  },
  props: {
    msg: String
  },
  data(){
    return{
      arrStockList:[
        {url: "https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo?serviceKey=V%2FHtK%2FBBbVsVhvItfI81qXSFRP150yZKElAO9iUf4z%2B1dc%2FTv%2FhFBG%2Bnfruy1CK7WHIkKt7516ZDs5s28NGgqA%3D%3D&numOfRows=2&pageNo=1&resultType=json&likeItmsNm=NAVER",Name :"네이버",Data:"" },
        {url: "https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo?serviceKey=V%2FHtK%2FBBbVsVhvItfI81qXSFRP150yZKElAO9iUf4z%2B1dc%2FTv%2FhFBG%2Bnfruy1CK7WHIkKt7516ZDs5s28NGgqA%3D%3D&numOfRows=2&pageNo=1&resultType=json&likeItmsNm=%EC%B9%B4%EC%B9%B4%EC%98%A4",Name :"카카오",Data:"" },
        {url: "https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo?serviceKey=V%2FHtK%2FBBbVsVhvItfI81qXSFRP150yZKElAO9iUf4z%2B1dc%2FTv%2FhFBG%2Bnfruy1CK7WHIkKt7516ZDs5s28NGgqA%3D%3D&numOfRows=2&pageNo=1&resultType=json&likeItmsNm=%EC%9D%B8%ED%8F%AC%EB%B0%94%EC%9D%B8",Name :"인포바인",Data:"" },
        {url: "https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo?serviceKey=V%2FHtK%2FBBbVsVhvItfI81qXSFRP150yZKElAO9iUf4z%2B1dc%2FTv%2FhFBG%2Bnfruy1CK7WHIkKt7516ZDs5s28NGgqA%3D%3D&numOfRows=2&pageNo=1&resultType=json&likeItmsNm=%ED%97%A5%ED%86%A0%EC%9D%B4%EB%85%B8%EB%B2%A0%EC%9D%B4%EC%85%98",Name :"헥토이노베이션",Data:"" },

      ],
      headline:"",
    }
  },
  methods:{
     getRandomInt:function(max) {
      return Math.floor(Math.random() * max);
      }
  },
  created() {

    this.arrStockList.forEach((item) => {




      axios.get(this.$parent.serverURL + this.$parent.getURLEncode(item.url), {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
        },
      })
          .then(response => {

            var stockData = response.data.response.body.items.item

            this.headline += "    • "  + stockData[0].itmsNm + ":"+ stockData[0].clpr.replace(/\B(?=(\d{3})+(?!\d))/g, ',')

          });

    })
    this.loaded = true;





  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer{

}
</style>
