<template>
  <div class="trendsArea">
    <div class="trendsAreaTitle">
      트랜드정보
    </div>
    <div class="trendsAreaContents">
      <table class="trendsAreaContentsTable">
        <template v-for="(item, i) in trandData" v-bind:key="i">
          <tr>
            <td>
              {{ item.order }}
            </td>
            <td>
              <img v-bind:src=item.imgSrc>
            </td>
            <td>
              {{ item.title }} <br>
              {{ item.contents }}
            </td>

          </tr>
        </template>

      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import xmlParser from "fast-xml-parser";

export default {
  name: "ExchangeRadeCompo"
  ,data() {
    return {
      loaded:false,
      trandData:[],

    }
  },
  created() {



      axios.get(this.$parent.serverURL + this.$parent.getURLEncode("https://trends.google.co.kr/trends/trendingsearches/daily/rss?geo=KR"), {
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept"
        },
      })
      .then(response => {
        const parser = new xmlParser.XMLParser();
        var xml = response.data.replaceAll("<BR>", "")
            .replaceAll("<br>", "")
            .replaceAll("ht:picture", "picture")
            .replaceAll("ht:news_item", "news_item")
        var data = parser.parse(xml)
        var items = data.rss.channel.item
        var date = new Date()
        var orderIndex = 0
        for(var i=0;i<5;i++) {
          // console.log(items[i].news_item.length)
          var news_item_snippet = ""
          if(Array.isArray(items[i].news_item) == true){
            news_item_snippet = items[i].news_item[0].news_item_snippet
          }else{
            news_item_snippet = items[i].news_item.news_item_snippet
          }

          if(date.getDate() == (new Date(items[i].pubDate).getDate())){
            orderIndex = orderIndex + 1
            console.log("true")
          }else{
            date = new Date(items[i].pubDate)
            orderIndex=1
          }

          this.trandData.push({
               order : orderIndex,
               title: items[i].title,
               contents: news_item_snippet,
               pubDate: items[i].pubDate,
               imgSrc: items[i].picture
             })

        }
        this.load = true

      });





  }

}
</script>

<style scoped>

.trendsArea{
  /*margin-left: 100px;*/
  padding-top: 10px;
  padding-left:20px;
}

.trendsArea .trendsAreaTitle {

  font-size: 12px;

  margin-left: 10px;
  text-align: left;
  padding-top: 10px;
  /*border-bottom: 3px #888;*/
  /*border-bottom-style: double ;*/
  /*width: 100px;*/

}

.trendsArea .trendsAreaContents {
  float: left;

  font-size: 12px;

  margin-left: 10px;
  text-align: left;
  padding-top: 10px;
  /*border-bottom: 3px #888;*/
  /*border-bottom-style: double ;*/
  /*width: 100px;*/

}

.trendsArea .trendsAreaContents .trendsAreaContentsTable{
  width: 100%;
  border: 1px solid #444444;
  border-collapse: collapse;
}




</style>
